<template>
  <div ref="dropDown">
    <label>Образование:</label>
<!--    <input type="text" v-model="educationValue" @input="$emit('update:modelValue', educationValue)" />-->
    <div @click="isDropDownVisible=true">
      {{mappedSelectedEducationVars}}
    </div>
    <div v-if="isDropDownVisible">
      <p v-for="(educationVar, index) in educationVars" :key="index" @click="toggleSelectedEducationVars(educationVar)">
        {{ educationVar.name || educationVar }}
      </p>
    </div>
<!--    v-show="!SelectedEducationVars"-->
    <div v-show="SelectedEducationVars!==null && SelectedEducationVars.name!=='Среднее'">
      <label>Учебное заведение:</label>
      <input type="text" v-model="educationData.institution" @input="updateModel" />
      <label>Факультет:</label>
      <input type="text" v-model="educationData.faculty" @input="updateModel" />
      <label>Специализация:</label>
      <input type="text" v-model="educationData.specialization" @input="updateModel" />
      <label>Год окончания:</label>
      <input type="text" v-model="educationData.yearOfGraduation" @input="updateModel" />
    </div>

  </div>
</template>

<script>
import { ref, computed, onMounted, onBeforeUnmount} from 'vue'

export default {
  props: {
    modelValue: Object,
    educationVars: {
      type: Array,
      default(){
        return []
      }
    }
  },
  setup(props, {emit}) {
    const SelectedEducationVars=ref(null)
    // const educationValue = ref(props.modelValue)
    // const educationalInstitutionValue = ref(props.modelValue)
    // const facultyValue = ref(props.modelValue)
    // const specializationValue = ref(props.modelValue)
    // const yearOfGraduationValue = ref(props.modelValue)
    const isDropDownVisible=ref(false)
    const dropDown=ref(null)
    const educationData = ref({
      education: '',
      institution: '',
      faculty: '',
      specialization: '',
      yearOfGraduation: '',
    });
    const updateModel = () => {
      if (!educationData.value.institution || !educationData.value.faculty || !educationData.value.specialization || !educationData.value.yearOfGraduation) {
        //errorMessage.value = 'Пожалуйста, заполните все поля!';
        return; // Не обновляем модель, если есть пустые поля
      }

      //errorMessage.value = ''; // Сбрасываем сообщение об ошибке, если все заполнено
      emit('update:modelValue', { selected: SelectedEducationVars.value, ...educationData.value });
    };
    const toggleSelectedEducationVars = (educationVar) => {
      SelectedEducationVars.value=educationVar;
      isDropDownVisible.value=false;
      educationData.value.education=educationVar
      if(SelectedEducationVars.value.name==='Среднее'){
        educationData.value.institution=1;
        educationData.value.faculty=1;
        educationData.value.specialization=1;
        educationData.value.yearOfGraduation=1;
      }
      if(SelectedEducationVars.value.name!=='Среднее'){
        educationData.value.institution=null;
        educationData.value.faculty=null;
        educationData.value.specialization=null;
        educationData.value.yearOfGraduation=null;
      }
      updateModel();

      // emit('update:modelValue', SelectedEducationVars);
    }
    const mappedSelectedEducationVars = computed(() => {
      return SelectedEducationVars.value?.name || SelectedEducationVars.value || 'Выбери';
    })
    // watch(() => props.modelValue, (newVal) => {
    //   // educationValue.value = newVal
    // })
    const closeDropDown=(element)=>{

      if(!dropDown.value.contains(element.target)){
        isDropDownVisible.value=false;
      }
    }
    onMounted(()=>{
      window.addEventListener('click', closeDropDown)
    })
    onBeforeUnmount(()=>{
      window.removeEventListener('click', closeDropDown)
    })
    return { closeDropDown,educationData, updateModel,  SelectedEducationVars, toggleSelectedEducationVars,  mappedSelectedEducationVars, isDropDownVisible, dropDown}
  }
}
</script>



