<template>
  <div>
    <label>Город:</label>
    <input type="text" v-model="cityValue" @input="$emit('update:modelValue', cityValue)" />
  </div>
</template>

<script>
import { ref, watch } from 'vue'

export default {
  props: {
    modelValue: String
  },
  setup(props) {
    const cityValue = ref(props.modelValue)

    watch(() => props.modelValue, (newVal) => {
      cityValue.value = newVal
    })

    return { cityValue }
  }
}
</script>